.btn {
	display: inline-block;
	padding: 8px 18px;
	color: $color-widget;
	font-size: 18px;
	line-height: 24px;
	text-align: center;
	text-decoration: none;
	border: 0;
	border-radius: 15px;
	transition: $transition;
	cursor: pointer;
	position: relative;

	//&-white
	//	color: $color-text
	//	background-color: $color-white
	//
	//	&:hover,
	//	&:focus,
	//	&:active
	//		color: $color-blue
	//
	//&-blue
	//	color: $color-white
	//	background-color: $color-accent
	//
	//	&:hover,
	//	&:focus,
	//	&:active
	//		background-color: #556CEF

	&-demo {
		padding: 7px 18px;
		color: $color-green-light;
		background-color: transparent;
		border: 1px solid rgba($color-green-light, .3);
		box-sizing: border-box;

		&:hover,
		&:focus,
		&:active {
			color: $color-white; } }

	&-play {
		background-color: $color-orange;

		&:hover,
		&:focus,
		&:active {
			box-shadow: 5px 5px 10px rgba(253, 191, 80, .25); } }

	&-review {
		padding: 7px 18px;
		color: $color-green-light;
		background-color: transparent;
		border: 1px solid rgba($color-green-light, .3);
		box-sizing: border-box;

		&:hover,
		&:focus,
		&:active {
			color: $color-white; } }

	&-site {
		background-color: $color-green-light;

		&:hover,
		&:focus,
		&:active {
			box-shadow: 5px 5px 10px rgba(214, 212, 175, 0.25); } }

	&-border {
		padding: 13px 20px;
		color: $color-text;
		background-color: $color-white;
		border: 2px solid $color-border;

		&:hover {
			color: $color-blue; } }

	&-more {
		padding: 0;
		color: $color-green-light;
		font-size: 16px;
		line-height: 22px;
		text-decoration: underline;

		&:hover {
			text-decoration: none; } }

	&-main {
		background-color: $color-orange;

		&:hover,
		&:focus,
		&:active {
			box-shadow: 5px 5px 10px rgba(253, 191, 80, 0.25); } }

	&-light {
		color: $color-dark; }

	&-submit {
		color: $color-text;
		background-color: #FFC529;

		&:hover,
		&:focus,
		&:active {
			box-shadow: 5px 5px 10px rgba(207, 159, 31, 0.6); } }

	&-big {
		padding: 15px 42px;
		@include r(1259) {
			padding: 15px 30px; } }

	&-fullwidth {
		width: 100%;
		text-align: center; } }

